	import common from '../../common/common.js';
	import utils from '../../common/util.js';
	export default {
		data() {
			return {
				DialogGoods: false,
				DialogPingjia:false,
				userObj: {},
				tableList: [{
						id: 1,
						nailArtistId: 1,
						startTimeDesc: '2019-09-01',
						endTimeDesc: "2019-09-01",
						nailArtist: {}
					},
					{
						id: 2,
						nailArtistId: 1,
						startTimeDesc: '2019-09-01',
						endTimeDesc: "2019-09-01",
						nailArtist: {}
					}
				],
				orderMain: {comment:""}
			};
		},
		onReady() {
			let userObj = uni.getStorageSync('userObj');
			this.userObj = userObj
		},
		onShow() {
			this.getTableList()
		},
		mounted() {
			this.getTableList()
		},
		methods: {
			goodsDetail: function(item) {
				this.DialogGoods = true
				this.orderMain = item
				
			},
			hideModal:function(){
				this.DialogGoods = false
				this.DialogPingjia = false
			},
			getTableList: function() {
				uni.request({
					url: common.webUrl + 'ordermain/getAllList',
					data: {
						search: {
							userId: this.userObj.id
						}
					},
					method: 'GET',
					dataType: '',
					success: (res) => {
						if (res.data.resCode == '0000') {
							this.tableList = res.data.resultList
						}
					}
				});
			},
			cancleAppoint: function(item) {
				item.state = 2
				uni.request({
					url: common.webUrl + 'appointment/changeState',
					data: item,
					method: 'POST',
					dataType: 'json',
					success: (res) => {
						if (res.data.resCode == '0000') {
							uni.showToast({
								title: "取消成功"
							})
						}
					}
				});
			},
          changeState: function(item,state) {
				item.state = state
				uni.request({
					url: common.webUrl + 'ordermain/changeState',
					data: item,
					method: 'POST',
					dataType: 'json',
					success: (res) => {
						if (res.data.resCode == '0000') {
							uni.showToast({
								title: "操作成功"
							})
						}
					}
				});
			},
			pingjia:function(item){
				this.DialogPingjia = true;
				this.orderMain = item;
				this.orderMain.comment = "";
			},
			saveComment:function(){
				console.log(this.orderMain)
				uni.request({
					url: common.webUrl + 'comment/saveCommnet',
					data: this.orderMain,
					method: 'POST',
					dataType: 'json',
					success: (res) => {
						if (res.data.resCode == '0000') {
							this.changeState(this.orderMain,5)
							this.hideModal()
						}
					}
				});
			}
		}
	}
